import React from 'react';
import { Link } from 'gatsby';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { Header } from 'components';
import { Layout, Container } from 'layouts';
import styled from '@emotion/styled';

const StyledLink = styled(Link)`
  color: ${props => props.theme.colors.black.base};
  box-shadow: inset 0 -2px 0 ${props => props.theme.colors.primary.base};
  border-bottom: 1px solid ${props => props.theme.colors.primary.base};
  transition: ${props => props.theme.transitions.default.transition};
  text-decoration: none;
  &:hover,
  &:focus {
    background: ${props => props.theme.colors.primary.base};
    color: ${props => props.theme.colors.white.light};
  }
`;

const ErrorPage = center => (
  <Layout>
    <Helmet title={'404'} />
    <Header title="404" />
    <Container center={center}>
      <h1>Woops, something went wrong.</h1>
      <h3>This page does not exist or is no longer reachable.</h3>
      <h3>
        You can return to the <StyledLink to="/">Homepage</StyledLink>.
      </h3>
    </Container>
  </Layout>
);

export default ErrorPage;

ErrorPage.propTypes = {
  center: PropTypes.object,
};
